import { HlcSrvAPI } from './hlcsrv_api';
import {debug, info,  error} from './util/log';
import { App } from './app';
import { afterDocLoad } from "./onload";
import * as jskbd from "jskbd"; 
import * as MsgBox from "./util/msgbox";
import {Client} from "./client";

let enabled :boolean = false;

function enable(){
  enabled = true;
}

function disable(){
  enabled = false;
}

function isEnabled(): boolean{
  return enable && !MsgBox.isOpen();
}

function init(doc: Document){
  // let app = new App(doc);
  let api = new HlcSrvAPI(doc.defaultView, "http://localhost:23333");
  let app = new Client(1, doc, api);
  doc.addEventListener("mouseup", ()=>{
    if (isEnabled()){
      app.highlightSelection();
    }
  });
  MsgBox.init();
  addShortcuts(app);
  info("hlc inited.");
}

function addShortcuts(app: App){
  jskbd.setShortcut([jskbd.ALT_LEFT], "KeyV", ()=>{
    if (!enabled && app.highlightSelection()){
      return;
    } else {
      enabled = !enabled;
      debug(`Highlighter enabled: ${enabled}`);
    }
  });
  jskbd.setShortcut([jskbd.ALT_LEFT], "KeyG", ()=>{
    let md = app.generateMarkdownNotes();
    MsgBox.show(md);

  });
  jskbd.start(window);
}

afterDocLoad(()=>{
  init(document);
});


