import { DecoratedBlock } from './decorator/decorated_block';
import { RangeMeta } from './core/range_meta';
import { restoreBlock } from './core/selector';
import { App } from './app';
import * as MSG from "./compiled/proto.js"
import { debug } from "./util/log";

export interface ServerAPI {
  get(uid: number, url: string, callback: (pagenote: MSG.hlcmsg.Pagenote) => void): void
  save(n: MSG.hlcmsg.Pagenote, callback: (savedIdList: MSG.hlcmsg.IdList) => void): void;
  delete(list: MSG.hlcmsg.IdList, callback: (removedIdList: MSG.hlcmsg.IdList) => void): void
}

export class Client extends App {

  constructor(uid: number, doc: Document, api: ServerAPI) {
    super(doc);
    this.m_uid = uid;
    this.m_api = api;
    super.setOnHighlightHandler(this.onNewHighlight.bind(this));
    // this.reload();
  }

  onNewHighlight(blk: DecoratedBlock){
    /**
     * {
      uid: this.m_uid,
      pageid: this.m_pid,
      highlights: [blk.rangeMeta]
    }
     */
    let n = new MSG.hlcmsg.Pagenote();
    n.uid = this.m_uid;
    n.pageid = this.m_pid;
    n.url = window.location.toString();
    debug(`n.url: ${n.url}`);
    n.highlights.push(metaToMsg(blk.rangeMeta));
    this.m_api.save(n, (savedIdList)=>{
      blk.setId(savedIdList.arr[0].toString());
    });
  }

  reload() {
    this.m_api.get(
      this.m_uid,
      this.m_win.location.toString(),
      (pagenote: MSG.hlcmsg.Pagenote) => {
        super.removeAll();
        if (this.m_pid < 1) {
          this.setPid(pagenote.pageid);
        }
        for (let meta of pagenote.highlights) {
          let bid = meta.id.toString();
          super.restoreHighlight(meta as RangeMeta, bid);
        }
      });
  }

  delete(blk: DecoratedBlock | string){
    let id :string;
    if (blk instanceof DecoratedBlock){
      id = blk.id;
    } else {
      id = blk;
    }
    this.m_api.delete(
      new MSG.hlcmsg.IdList({arr:[Number(id)]}),
      (list)=>{
        super.removeHighlight(id);
      }
    );
  }

  private setPid(id: number) {
    this.m_pid = id;
  }

  private m_api: ServerAPI;
  private m_uid: number;
  private m_pid: number;
}

function metaToMsg(meta: RangeMeta):MSG.hlcmsg.RangeMeta{
  return MSG.hlcmsg.RangeMeta.create({
    anchor: meta.anchorUPath,
    start : meta.startNodeUPath,
    startOffset: meta.startCharIndex,
    end : meta.endNodeUPath,
    endOffset : meta.endCharIndex
  });
}

function msgToMeta(m: MSG.hlcmsg.RangeMeta): RangeMeta{
  return new RangeMeta([m.anchor, m.start, m.end, m.text], [m.startOffset, m.endOffset]);
}


