import {warn, debug,  error,  info} from './util/log';
import { ServerAPI } from './client';
import * as MSG from './compiled/proto.js';

export class HlcSrvAPI implements ServerAPI{

  constructor(win:Window, urlBase: string){
    this.m_base = urlBase;
    this.m_get  = urlBase + "/pagenote";
    this.m_new  = urlBase + "/pagenote/new";
    this.m_del  = urlBase + "/pagenote/delete";
  }

  public get(uid: number, 
             url: string, 
             callback: (pagenote: MSG.hlcmsg.Pagenote) => void): void {
    let req = `${this.m_get}?uid=${uid}&url=${encodeURI(url)}`;
    fetch(req).then(
      (resp: Response)=>{
        resp.body.getReader().read().then(
          (v)=>{
            let resp = MSG.hlcmsg.HlcResp.decode(v);
            if (resp.code == MSG.hlcmsg.HlcResp.RespCode.SUC){
              if (resp.payloads){
                let plds = resp.payloads;
                if (plds.length < 1){
                  debug("server returned no pagenote");
                  return;
                }
                let pagenote = MSG.hlcmsg.Pagenote.decode(
                  plds[0].value, plds[0].value.length);
                callback(pagenote);
              } else {
                debug("server returned no payloads");
              }
            } else {
              error(resp.msg);
            }
          }
        )
    }, (reason: any)=>{
      error(reason);
    });
  }

  public save(n: MSG.hlcmsg.Pagenote$Properties, callback: (savedIdList: MSG.hlcmsg.IdList) => void): void {
    
    let jn = JSON.parse(JSON.stringify(n));
    debug(`saving notes 2: ${JSON.stringify(jn)}`);
    let msg = MSG.hlcmsg.Pagenote.fromObject(jn);
    debug(`saving notes 3: ${JSON.stringify(msg)}`);
    let data = MSG.hlcmsg.Pagenote.encode(msg).finish();
    debug(`encoded = ${Array.prototype.toString.call(data)}`);
    if (data.length < 1){
      warn("will not post empty buf");
      return;
    }
    HlcSrvAPI.post(this.m_new, data, (resp)=>{
      let r = MSG.hlcmsg.HlcResp.decode(resp);
      info(`Got response: ${JSON.stringify(r)}`);
    });
  }

  public delete(list: MSG.hlcmsg.IdList, callback: (removedIdList: MSG.hlcmsg.IdList) => void): void {
    throw new Error('Not implemented yet.');
  }

  public setUID(id: number){
    this.m_uid = id;
  }

  public setPid(id: number){
    this.m_pid = id;
  }

  private static post(url: string, data: any, onsuc:(resp:any)=>void){
    let req = new XMLHttpRequest();
    req.open("POST", url, true);
    req.onreadystatechange = function(){
      if (req.readyState == XMLHttpRequest.DONE && (req.status >= 200 && req.status < 305)){
        debug(`response text: ${req.responseText}`);
        onsuc(req.response);
      } else {
        error(`POST: ${url} | ${req.status}, ${req.statusText}`);
      }
    };
    req.send(data);
  }

  // private postMessage
  private m_base: string;
  private m_get : string;
  private m_new : string;
  private m_del : string;
  private m_uid : number;
  private m_pid : number;
}


