import * as Markdown from './util/markdown';
import { BasicDecorator } from './decorator/decorator';
import { debug, warn } from './util/log';
import { Block } from './core/block';
import { extractSelectedBlock, restoreBlock } from './core/selector';
import * as React from 'react';
import * as ReactDOM from 'react-dom';

import { Footnote } from './components/Footnote';
import { DecoratedBlock, DecoratedBlockFactory } from './decorator/decorated_block';
import { findPositionAnchor, getNodeByPath } from './util/dom_helper';
import { RangeMeta } from "./core/range_meta";

interface FootnoteDict {
  [key: string]: Footnote
}

export interface OnHighlight {
  (block: DecoratedBlock): void
}

/**
 * A demo app generate highlights of selected text.
 */
export class App {

  constructor(doc: Document) {
    this.m_ftnotes = {} as FootnoteDict;
    this.m_doc = doc;
    this.m_win = doc.defaultView;
  }

  /**
   * If user selected some text in the current window, highlight it.
   * Else do nothing.
   * @return the generated Block id or null.
   */
  highlightSelection(): string {
    let block: Block = extractSelectedBlock(window, document);
    if (block == null) {
      return null;
    }
    let decoratedBlock = this.decorate(block);
    this.addBlock(decoratedBlock);
    if (this.m_onHighlight) {
      this.m_onHighlight(decoratedBlock);
    }
    return decoratedBlock.id;
  }

  /**
   * @param handler called when a new DecoratedBlock object is built.
   * It is not guarenteed to be called when the block is rendered in DOM.
   */
  setOnHighlightHandler(handler: OnHighlight) {
    this.m_onHighlight = handler;
  }

  /**
   * Rebuild the block with a given meta
   * @param meta See @class{RangeMeta}
   * @param id optional, the id for the restored block
   * @return restored block id
   */
  restoreHighlight(meta: RangeMeta, id?: string) {
    let block = restoreBlock(this.m_win, this.m_doc, meta, id);
    let decoratedBlock = this.decorate(block);
    this.addBlock(decoratedBlock);
    return decoratedBlock.id;
  }

  /**
   * Find and remove the block with a given id.
   * @param id of the block to remove
   */
  removeHighlight(id: string): void {
    for (let ftId in this.m_ftnotes) {
      let note = this.m_ftnotes[ftId];
      if (note.removeHighlight(id)) {
        return;
      }
    }
    warn(`removeHighlight: ${id} not found in any footnotes`);
  }

  removeAll(): void{
    for (let ftId in this.m_ftnotes) {
      let note = this.m_ftnotes[ftId];
      note.removeAll();
    }
  }

  getAllText(): string[] {
    let notes: string[] = [];
    for (let key in this.m_ftnotes) {
      let note = this.m_ftnotes[key];
      let blocks = note.getAll();
      for (let blk of blocks) {
        notes.push(blk.text);
      }
      notes.push("");
    }
    return notes;
  }

  generateMarkdownNotes(): string {
    let title = this.m_doc.title || this.m_doc.head.title;
    let url = this.m_win.location.toString();
    let lines = this.getAllText();
    let noteTitle = `Notes on ${Markdown.link(title, url)}\r\n\r\n`;
    let md = Markdown.h(1, noteTitle);
    let buf = [];
    for (let idx = 0; idx <= lines.length; idx++) {
      if (idx == lines.length || lines[idx] == "") {
        if (buf.length > 0) {
          md += Markdown.ul(buf, 0);
        }
        md += "\r\n";
        buf.length = 0;
      } else {
        buf.push(lines[idx]);
      }
    }
    return md;
  }

  private decorate(block: Block): DecoratedBlock {
    let factory = new DecoratedBlockFactory(block);
    factory.addDecorator(BasicDecorator);
    return factory.make();
  }

  private addBlock(blk: DecoratedBlock): void {
    let ftntId = blk.rangeCache.meta.anchorUPath;
    if (ftntId in this.m_ftnotes) {
      let ftnt: Footnote = this.m_ftnotes[ftntId];
      ftnt.addHighlight(blk);
      debug(`added to existing ftnt: ${ftntId}`);
    } else {
      this.renderFootnote(ftntId, [blk]);
    }
  }

  private getOrCreateContainer(cid: string, parent: Node): HTMLElement {
    let d = this.m_doc.getElementById(cid);
    if (!d) {
      d = this.m_doc.createElement("div");
      d.id = cid;
      parent.appendChild(d);
    }
    return d;
  }

  private renderFootnote(anchorUPath: string, initBlocks: DecoratedBlock[]): void {
    debug(`generating new ftnt under : ${anchorUPath}`);
    let anchor: HTMLElement =
      getNodeByPath(this.m_doc, anchorUPath) as HTMLElement;
    const containerId: string = "ftnt_" + anchorUPath;
    let container = this.getOrCreateContainer(containerId, anchor);
    let ftntEl = React.createElement(Footnote, {
      id: anchorUPath,
      source: { get: () => initBlocks, poll: false },
      onMount: (ins: Footnote) => {
        this.m_ftnotes[anchorUPath] = ins;
      }
    })
    debug("generated under", container);
    ReactDOM.render(ftntEl, container);
  }


  protected m_ftnotes: FootnoteDict;
  protected m_doc: Document;
  protected m_win: Window;
  protected m_onHighlight: OnHighlight;
}



